// @flow
import React from 'react';
import type { Element } from 'react';
import Table from 'components/Table';
import Loader from 'components/Loader';
import { ActionButtons } from './ActionButtons';
import { hideIfInvalidProps } from 'utils/RenderComponent';

type Props = {
  offers: Array<Object>,
  isLoading: boolean,
  handleApproval: Function,
  isSubmitting: boolean,
  handleDecline: Function,
};

const checkProps = ({ offers }) => offers && offers.length;

const SellerOfferList = (props: Props): Element<'div'> => {
  const {
    offers,
    isLoading,
    handleApproval,
    isSubmitting,
    handleDecline,
  } = props;

  const renderItem = ({ cell: { value } }) => <span>{value}</span>;

  const renderActions = ({ cell }) => (
    <ActionButtons
      cell={cell}
      handleApproval={handleApproval}
      isSubmitting={isSubmitting}
      handleDecline={handleDecline}
    />
  );

  const columns = [
    {
      Header: 'ID',
      accessor: 'OfferId',
      Cell: renderItem,
    },
    {
      Header: 'Offer Price',
      accessor: 'CounterOffer.Price',
      Cell: renderItem,
    },
    {
      Header: 'Loan Type',
      accessor: 'Funding.LoanType',
      Cell: renderItem,
    },
    {
      Header: 'Funding',
      accessor: 'Funding.PurchaseType',
      Cell: renderItem,
    },
    {
      Header: 'Down Payment',
      accessor: 'Funding.DownPayment',
      Cell: renderItem,
    },
    {
      Header: 'Due Diligence Contingency',
      accessor: 'DueDiligenceContingency',
      Cell: renderItem,
    },
    {
      Header: 'Sale of Home Contingency',
      accessor: 'SaleOfHomeContingency',
      Cell: renderItem,
    },
    {
      Header: 'Furniture',
      accessor: 'Furniture',
      Cell: renderItem,
    },
    {
      Header: 'Closing Period',
      accessor: 'ClosingPeriod',
      Cell: renderItem,
    },
    {
      Header: 'Current Status',
      accessor: 'Step.messages.seller',
      Cell: renderItem,
    },
    {
      Header: 'Actions',
      Cell: renderActions,
    },
  ];

  return (
    <div className="offer_block">
      <div className="offer_container">
        {isLoading && <Loader />}
        <Table columns={columns} data={offers} />
      </div>
    </div>
  );
};

export default (hideIfInvalidProps(checkProps)(
  SellerOfferList,
): typeof SellerOfferList);
